@font-face {
	font-family: 'Final Fantasy';
	src: url('New.eot');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Final Fantasy';
	src: url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
		url('New.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

$title: 'Final Fantasy';  

@mixin testLook {
    position: relative;
    border: dotted 1px red;
    background: rgba(0, 0, 0, .04);
    color: rgba(0, 0, 0, .5);
}

@mixin flexRowCenter {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

@mixin flexRow {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
}
@mixin test-view {
    border: 1px dotted red;
    background: rgba(0, 0, 0, .02);
}
@mixin remove-decorations {
    text-decoration: none;
    text-decoration-line: none;
    text-decoration-style: none;
    color: inherit;
    cursor: pointer;
    font-family: inherit;
}

@mixin text-stroke {
-webkit-text-stroke: 2px black;
   text-shadow:
     -1px -1px 0 #000,  
      1px -1px 0 #000,
      -1px 1px 0 #000,
       1px 1px 0 #000;
}

@mixin button-style {
    font-size: 18px;
}

@mixin center-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}